.SharedQuiz_Frame {
  margin: auto;
  width: 90%;

  height: calc(100vh - 120px);

  display: flex;
  flex-direction: column;
  justify-content: center;
}

.text {
  font-size: 150%;
  font-family: "Binggrae";
  white-space: pre-wrap;
}

.SharedQuiz_Frame .btn {
  margin-top: 10px;
}

@font-face {
  font-family: "Binggrae";
  src: url("./fonts/Binggrae.otf");
}
