.Category_list {
  margin-top: 15px;
  margin-bottom: 15px;
  display: flex;
  flex-wrap: nowrap;
  overflow-x: hidden;
  white-space: nowrap;
  width: inherit;
  justify-content: flex-start;
}

.item {
  margin-left: 3px;
  margin-right: 3px;
}

.chip-label {
  font-family: "Noto Sans KR", sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
}

@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR&display=swap");
